.card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: var(--white);
  border-radius: 1rem;
}
@media (max-width: 23.125em) {
  .card {
    width: 90%;
    font-size: 90%;
  }
}
.card__img {
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
  height: 100%;
  max-width: 100%;
}
.card h2,
.card p,
.card .container {
  padding-right: 3.75rem;
  padding-left: 3.75rem;
}
.card .container {
  display: grid;
  grid-template-columns: 0.5fr 2fr 0.5fr;
  align-items: center;
  margin-bottom: 1.875rem;
}
.card__michelle {
  height: 3.75rem;
  width: 3.75rem;
  border-radius: 9999rem;
}
.card .org {
  margin-right: 3.75rem;
  flex-direction: column;
  margin-left: 1.25rem;
}
.card .share {
  top: 75%;
  left: 0;
  width: 94%;
  height: 3.0625rem;
  border-radius: 0rem 0rem 0.625rem 0.625rem;
}
.card .share .shareSvg {
  width: 35%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-right: 15%;
}
.card .share #buttonCloseOne {
  background: var(--ltgrayblue);
  border-radius: 3.125rem;
  padding: 0.5rem;
  margin-right: 4%;
  display: inline-block;
  cursor: pointer;
  outline: none;
  color: var(--white);
  border: none;
}
.card .share .triangle {
  display: none;
}
@media (min-width: 46.75em) {
  .card {
    position: fixed;
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: auto;
    width: auto;
  }
  .card__img {
    justify-self: center;
    height: 100%;
    width: 100%;
    border-top-right-radius: 0;
    border-bottom-left-radius: 1rem;
  }
  .card .container #button {
    background: var(--ltgrayblue);
    border-radius: 3.125rem;
    padding: 0.5rem;
    margin-right: 4%;
    display: inline-block;
    cursor: pointer;
    outline: none;
    fill: var(--vdkgrayblue);
    border: none;
  }
  .card .share {
    background-color: var(--vdkgrayblue);
    color: var(--grayblue);
    fill: var(--white);
    display: flex;
    grid-area: footer;
    place-content: center;
    border-radius: 1rem;
    width: 24.8rem;
    padding: 2rem 3.6rem;
    bottom: 105%;
    left: 60%;
  }
  .card .share .facebook, .card .share .twitter {
    margin-right: 0.9375rem;
  }
  .card .share .facebook {
    margin-left: 0.9375rem;
  }
  .card .share .shareSvg {
    display: flex;
    align-items: center;
    width: 44%;
    justify-content: space-evenly;
  }
  .card .share .triangle {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -10px;
    border-width: 10px;
    border-style: solid;
  }
  .card .container #buttonClose {
    background: var(--desdkblue);
    border-radius: 3.125rem;
    padding: 0.5rem;
    margin-right: 4%;
    display: inline-block;
    cursor: pointer;
    outline: none;
    fill: var(--ltgrayblue);
    border: none;
    display: none;
  }
  .card .share #buttonCloseOne {
    display: none;
  }
}

html {
  font-size: 100%;
  box-sizing: border-box;
  line-height: 1.5;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  margin: 6.25rem 1.875rem 6.25rem 1.875rem;
  font-family: "Manrope", sans-serif;
  background: var(--ltgrayblue);
  height: 100vh;
  padding: 1.25rem 0.625rem 0 0.625rem;
}
body .attribution {
  font-size: 1.25rem;
  margin-top: 3.75rem;
  text-align: center;
  margin-bottom: 2.5rem;
}
body .attribution a {
  color: hsl(228, 45%, 44%);
}
@media (max-width: 23.125em) {
  body .attribution {
    width: 90%;
    font-size: 90%;
  }
}
@media (max-width: 23.125em) {
  body {
    padding: 0;
    margin: 0.9375rem 1rem;
    width: 90%;
    font-size: 90%;
  }
}

:root {
  --vdkgrayblue: hsl(217, 19%, 35%);
  --desdkblue: hsl(214, 17%, 51%);
  --grayblue: hsl(212, 23%, 69%);
  --ltgrayblue: hsl(210, 46%, 95%);
  --white: hsl(0, 0%, 100%);
}

h1,
h2,
h3 {
  margin-top: 0;
  margin-bottom: 0.625rem;
  overflow-wrap: break-word;
}

h1 {
  font-weight: 600;
}

h2 {
  padding-top: 3.75rem;
  font-size: 1.375rem;
  font-weight: 700;
  color: var(--vdkgrayblue);
}

a,
a:visited,
a:hover {
  cursor: pointer;
}

p {
  padding-top: 0.625rem;
  font-weight: 500;
  font-size: 0.9375rem;
  opacity: 50%;
  overflow-wrap: break-word;
  margin-bottom: 1.875rem;
}